import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/resume/:name',
      name: 'name',
      component: () => import('@/views/Resume')
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('@/views/Login')
    },
    {
      path: '/register',
      name: 'register',
      component: () => import('@/views/Register')
    },
    {
      path: '/editor',
      name: 'editor',
      component: () => import('@/views/Editor')
    },
    {
      path: '/resume',
      redirect: '/editor',
      name: 'resume',
      component: () => import('@/views/Editor')
    }
  ]
})
